* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}
.board {
    width: 350px;
    height: 350px;
    margin: 20px auto;
    display: grid;
    grid-template-columns: repeat(3, 30%);
    grid-template-rows: repeat(3 , 30%);
    justify-content: center;
    align-content: center;
    gap: 10px;
}
button {
    background-image: linear-gradient(135deg , #dbdbdb 10%, rgb(153, 153, 153));
    border: none;
    border-radius: 15px;
    font-family:'Fredoka One', cursive;
    font-size: 2.9rem;
    font-weight: 400;
    box-shadow: 0px 0px 1px #eceaea;
    transition: 0.1s;
}
button:hover {
    background-image: linear-gradient(135deg , #ecebeb 10%, rgb(173, 173, 173));
    transform: scale(1.05);
    box-shadow: 4px 4px 4px #969696;
}
.red {
    color: rgb(249 57 57);;
}
.blue {
    color: #215dcb;
}